.videos-box {
    width: 90%;
    margin-top: 20px;
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    padding: 20px;
    gap: 55px 2%;
    margin-inline: auto;

    &>div {
        border-radius: 10px;
        padding: 5px 2px 10px 5px;
        background-color: #E3651D;
        width: 90%;
        opacity: .5;

        & div {
            position: relative;

            & .video-number {
                display: flex;
                align-items: center;
                justify-content:  center;
                position: absolute;
                width: 40px;
                height: 40px;
                top: 0;
                right: 8px;
                padding: 10px;
                background-color: #fff;
                border-radius: 50%;
                color: #000;
                box-shadow: 1px 1px 1px 1px #000;
            }

            & video {
                width: 100%;
                aspect-ratio: 4 / 2;
                border-radius: 10px;
                object-fit: fill;
            }

            & video[poster] {
                width: 100%;
                height: 100%;
            }
        }

        & p {
            margin-top: 8px;
            font-weight: bold;
            text-shadow: 1px 1px 1px #000;
            font-size: 1.1em;
        }
    }

    &>div:hover {
        opacity: 1;
    }
}



@media screen and (max-width: 767px) {
    .videos-box {
        width: 98%;
        margin-top: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 20px;
        row-gap: 40px;
        margin-inline: auto;

        &>div {
            width: 100%;

            & video {
                width: 100%;
                aspect-ratio: 4 / 3.2;
                border-radius: 10px;
                object-fit: fill;
            }
        }
    }
}